import React,{Component,Fragment} from 'react';
// import CartItem from './CartItem';
// import $ from "jquery";
class Cart extends Component {

  render(){
    return (
        <Fragment>
            <table className="table product-table table-hover cart container" >
                <thead>
                    <tr>
                        <th className="col-1 text-center">Hnh nh</th>
                        <th className="col-2 text-center">Sn Phm</th>
                        <th className="col-1">Kch thc</th>
                        <th className="col-1">n gi</th>
                        <th className="col-1 text-center">S lng cn</th>
                        <th className="col-1 text-center">S Lng mua</th>
                        <th className="col-1">Tng Cng</th>
                        <th className="col-1 text-center">Hnh ng</th>
                    </tr>
                </thead>
                <tbody>
                   {this.props.children}
                </tbody>
            </table>
        </Fragment>
        
    );
  }
  
}


export default Cart;
