import React, { Component } from 'react';
import {Helmet} from 'react-helmet';
import axios from 'axios';
import { connect } from 'react-redux';
import * as actions from './../../actions/index';
import { withRouter } from 'react-router-dom';

// chi tit sn phm -- 
// ng nhp, ng k --- Phc
class LoginPage extends Component {
    handleLoginSubmit = (event) => {
        event.preventDefault();
        axios({
            method: 'POST',
            url: '/login',
            data: {
                username: document.getElementById('username').value,
                password: document.getElementById('password').value
            }
        }).then(res => {
            if(res && res.status === 200) {
                this.props.setToken(res.data.token);
                
                axios({
                    method: 'GET',
                    url: `/api/users/me`,
                    headers: {
                        "Authorization": `Bearer ${this.props.token}`
                    }
                }).then(res => {
                    if(res && res.status === 200) {
                        this.props.setAdmin(res.data.isAdmin);
                        let user = {
                            ...res.data
                        };
                        this.props.fetchCartByIdUserRequest(user.id_User);
                        this.props.fetchUserByIdRequest(user.id_User);
                        
                        user = JSON.stringify(user);
                        this.props.getUserLogin(user);
                        localStorage.setItem('user', user);
                        this.setState({
                            loading: false
                        });
                        this.props.history.push("/");
                    }
                });
            }
        }).catch(error => {
            if(error.response) {
                alert("Li: " + error.response.data.message)
            }
        })
    }

    render() {
        if (this.props.token){
            this.props.history.push("/");
        }
        return (
            
            <div className='container mt-2 mb-5'> 
                <Helmet>
                    <title>ng nhp</title>
                </Helmet>
                <form className="row">
                    <div className="col-md-6 p-4 shadow">
                        <div className="form-group">
                            <h2 className="text-center">ng nhp</h2>
                            <label for="username">ng nhp</label>
                            <input type="text" className="form-control" name="username" id="username" aria-describedby="helpId" placeholder="Tn ng nhp" />
                        </div>
                        <div class="form-group">
                            <label for="password">Mt khu</label>
                            <input type="password" class="form-control" name="password" id="password" aria-describedby="helpId" placeholder="Mt khu" /> 
                        </div>
                        <div className="text-center">
                            <button type="submit" class="btn btn-dark" onClick={this.handleLoginSubmit}>ng nhp</button>
                        </div>
                        <div className="pt-3 text-center text-secondary">
                            <a className="text-decoration-none" href="/"><i>Qun mt khu?</i></a>
                        </div>
                    </div>

                    <div className="col-md-6 p-5">
                        <h2>Ngi Mi? To Ti Khon</h2>
                        <p>Bng cch to ti khon vi ca hng ca chng ti, bn s c th thc hin 
                            quy trnh thanh ton nhanh hn, lu tr nhiu a ch giao hng, xem v 
                            theo di n t hng trong ti khon ca bn v hn th na.
                        </p>
                        <a href="/register"><button type="button" class="btn btn-primary">To ti khon mi</button></a>
                    </div>
                </form>
                
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        ...state.authorization
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setToken: (token) => {
            dispatch(actions.setToken(token));
        },
        setAdmin: (isAdmin) => {
            dispatch(actions.setAdmin(isAdmin));
        },
        getUserLogin: (user)=>{
            dispatch(actions.getUserLogin(user));
        },
        fetchCartByIdUserRequest:(id)=>{
            dispatch(actions.fetchCartByIdUserRequest(id));
        },
        fetchUserByIdRequest:(id)=>{
            dispatch(actions.fetchUserByIdRequest(id));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(LoginPage));