import React, { Component } from 'react';
import {Helmet } from 'react-helmet';
import axios from 'axios';
import { connect } from 'react-redux';
import * as actions from './../../actions/index';
import { withRouter } from 'react-router-dom';

class RegisterPage extends Component {
    handleRegister = (event) => {
        event.preventDefault();
        const data = {
            username: document.getElementById('username').value,
            password: document.getElementById('password').value, 
            email: document.getElementById('email').value,
            phone: document.getElementById('phone').value,
            address: document.getElementById('address').value, 
        }
        if(document.getElementById("password").value === document.getElementById("passwordConfirmed").value)
        (
            axios({
                method: 'POST',
                url: '/register',
                data
            }).then(res => {
                if(res && res.status === 200) {
                    if(window.confirm('To ti khon thnh cng, bn c mun ng nhp ngay?')) {
                        this.props.history.push('/login');
                    }
                }
            }).catch(error => {
                if(error.response) {
                    alert("Li: " + error.response.data.message)
                }
            })
        )
        else {
            alert("Mt khu bn nhp khng khp nhau")
        }
    }
    render() {
        return (
            <div className="container mt-2 mb-4">
                <Helmet>
                    <title>ng k</title>
                </Helmet>
                <form className="regForm mx-auto shadow p-4">
                    <h2 className="text-center">ng k</h2>
                    <p className="text-center"><u><a href="/login"> c ti khon? ng nhp ngay.</a></u></p>
                    <div class="form-group">
                        <label for="username">Tn ti khon</label>
                        <input type="text" name="username" id="username" class="form-control" placeholder="S in thoi" aria-describedby="helpId" /> 
                    </div>
                    <div class="form-group">
                        <label for="email">Email</label>
                        <input type="text" name="email" id="email" class="form-control" placeholder="Tn" aria-describedby="helpId" /> 
                    </div>
                    <div class="form-group">
                        <label for="password">Mt khu</label>
                        <input type="password" name="password" id="password" class="form-control" placeholder="Mt khu" aria-describedby="helpId" /> 
                    </div>
                    <div class="form-group">
                        <label for="passwordConfirmed">Xc nhn mt khu</label>
                        <input type="password" name="passwordConfirmed" id="passwordConfirmed" class="form-control" placeholder="Xc nhn mt khu" aria-describedby="helpId" /> 
                    </div>
                    <div class="form-group">
                        <label for="address">a ch</label>
                        <input type="text" name="address" id="address" class="form-control" placeholder="S in thoi" aria-describedby="helpId" /> 
                    </div>
                    <div class="form-group">
                        <label for="phone">S in thoi</label>
                        <input type="text" name="phone" id="phone" class="form-control" placeholder="S in thoi" aria-describedby="helpId" /> 
                    </div>
                    <div className="text-center">
                        <button class="btn btn-dark" onClick={this.handleRegister}>To ti khon mi</button>
                    </div> 
                </form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        ...state.authorization
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        setToken: (token) => {
            dispatch(actions.setToken(token));
        },
        setAdmin: (isAdmin) => {
            dispatch(actions.setAdmin(isAdmin));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(RegisterPage));